    font-family: "Euclid Square";
    src: url(../fonts/EuclidSquare-Regular.woff2) format("woff2"),
        url(../fonts/EuclidSquare-Regular.woff) format("woff"),
        url(../fonts/EuclidSquare-Regular.ttf) format("ttf"),
        url(../fonts/EuclidSquare-Regular.eot) format("eot");
    font-style: normal;
    font-weight: 400;
}

@font-face {
    font-family: "Euclid Square";
    src: url(../fonts/EuclidSquare-Medium.woff2) format("woff2"),
        url(../fonts/EuclidSquare-Medium.woff) format("woff"),
        url(../fonts/EuclidSquare-Medium.ttf) format("ttf"),
        url(../fonts/EuclidSquare-Medium.eot) format("eot");
    font-style: normal;
    font-weight: 500;
}

@font-face {
    font-family: "Euclid Square";
    src: url(../fonts/EuclidSquare-SemiBold.woff2) format("woff2"),
