        url(../fonts/EuclidSquare-SemiBold.eot) format("eot");
    font-style: normal;
    font-weight: 600;
}

@font-face {
    font-family: "Euclid Square";
    src: url(../fonts/EuclidSquare-Bold.woff2) format("woff2"),
        url(../fonts/EuclidSquare-Bold.woff) format("woff"),
        url(../fonts/EuclidSquare-Bold.ttf) format("ttf"),
        url(../fonts/EuclidSquare-Bold.eot) format("eot");
